.header{
  grid-area: header;
  background: rgb(236, 235, 235);
  padding: 13px;
  position: relative;
}
.header img{
  width: 80px;
}
.loginBlock{
  position: absolute;
  right: 10px;
  top: 15px;
}

.logout_block{
  width: 200px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.nick{
  color: white;
  text-decoration: none;
  font-size: 20px;
}
.nick:hover{
  color: blue;
}
.logout_btn{
  text-decoration: none;
  font-size: 15px;
  background: blue;
  color: white;
  border: 0;
  border-radius: 15px;
  padding: 5px 10px;
}
.logout_btn:hover{
  background: white;
  color: blue;
  cursor: pointer;
}
.login{
  font-size: 20px;
  color: white;
  text-decoration: none;
  list-style: none;
}
.login:hover{
  color: blue;
}